<script>
import {getCustomerDataAll, searchCustomerData} from 'api/customers'
import PageHeader from 'components/common/menu/PageHeader'
import CustomerForm from 'views/master/CustomerForm'


export default {
  name: 'CustomerList',
  components: {
    PageHeader,
    CustomerForm
  },
  data() {
    return {
      ruleForm: {
        CustomerName: '',
        Personnel: '',
        Telephone: '',
        CreateDate: '',
        Is_Delete: false
      },
      uuid: '',
      tableData: [{}],
      loading: false,
      dialogFormVisible: false
    }
  },
  created() {
    this.getCustomerDataAll()
  },
  methods: {
    getCustomerDataAll() {
      this.loading = true
      getCustomerDataAll().then(res => {
        this.tableData = res.data.data
        if (res.status == 200) {
          this.loading = false
        }
      }).catch(err => {
        console.log(err)
      })
    },
    searchCustomerData() {
      let values = {}
      values['CustomerName'] = this.ruleForm.CustomerName
      values['Personnel'] = this.ruleForm.Personnel
      values['Telephone'] = this.ruleForm.Telephone
      values['Is_Delete'] = this.ruleForm.Is_Delete
      searchCustomerData(values).then(res => {
        this.tableData = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchCustomerData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    closeDialog() {
      this.dialogFormVisible = false
    },
    reloadList() {
      this.dialogFormVisible = false
      this.getCustomerDataAll()
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.getCustomerDataAll()
    },
    handleEdit(index, row) {
      this.uuid = row.uuid
      this.dialogFormVisible = true
    },
    createBtn() {
      this.uuid = ''
      this.dialogFormVisible = true
    },
    handleDelete(index, row) {
      console.log(index, row)
    }
  }
}
</script>

