import {request} from 'network/request'

export function postCustomerData(paramenter) {
  return request({
    url: '/PostCustomerData',
    method: 'post',
    data: paramenter
  })
}

export function getCustomerDataAll(paramenter) {
  return request({
    url: '/GetCustomerDataAll',
    method: 'get',
    data: paramenter
  })
}

export function getCustomerDataOne(paramenter) {
  return request({
    url: '/GetCustomerDataOne',
    method: 'post',
    data: paramenter
  })
}

export function searchCustomerData(parameter) {
  return request({
    url: '/SearchCustomerData',
    method: 'post',
    data: parameter
  })
}
