<template>
  <div>
    <!--    <PageHeader>-->
    <!--      <template slot="title">戻る</template>-->
    <!--      <template slot="content">得意先登録</template>-->
    <!--    </PageHeader>-->
    <!--    <template>得意先登録及び編集の画面です。</template>-->
    <!--    <el-divider></el-divider>-->
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="CustomerName">
            <el-input v-model="ruleForm.CustomerName" placeholder="得意先名">
              <template slot="prepend">得意先名</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="CustomerCode">
            <el-input v-model="ruleForm.CustomerCode" placeholder="得意先CD">
              <template slot="prepend">得意先CD</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="Department1">
            <el-input v-model="ruleForm.Department1" placeholder="部署I">
              <template slot="prepend">部署I</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="Department2">
            <el-input v-model="ruleForm.Department2" placeholder="部署II">
              <template slot="prepend">部署II</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="Department3">
            <el-input v-model="ruleForm.Department3" placeholder="部署III">
              <template slot="prepend">部署III</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="Personnel">
            <el-input v-model="ruleForm.Personnel" placeholder="担当者">
              <template slot="prepend">担当者</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="Telephone">
            <el-input v-model="ruleForm.Telephone" placeholder="電話番号">
              <template slot="prepend">電話番号</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="Fix">
            <el-input v-model="ruleForm.Fix" placeholder="FIX">
              <template slot="prepend">FIX</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="PostalNumber">
            <el-input v-model="ruleForm.PostalNumber" placeholder="郵便番号">
              <template slot="prepend">郵便番号</template>
              <el-button slot="append" icon="el-icon-search" @click="searchAddress"></el-button>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="Address1">
            <el-input v-model="ruleForm.Address1" placeholder="都道府県">
              <template slot="prepend">都道府県</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="Address2">
            <el-input v-model="ruleForm.Address2" placeholder="市区町村">
              <template slot="prepend">市区町村</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="Address3">
            <el-input v-model="ruleForm.Address3" placeholder="建物">
              <template slot="prepend">建物</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20" type="flex" justify="end">
        <el-col :span="7">
          <el-form-item>
            <el-button-group>
              <!--            <el-button type="warning" @click="resetForm('ruleForm')">キャンセル</el-button>-->
              <el-button type="warning" @click="cancelForm()">キャンセル</el-button>
              <el-button type="primary" icon="el-icon-finished" @click="submitForm('ruleForm')">登録</el-button>
            </el-button-group>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import {getAddress} from 'api/postal'
import {postCustomerData, getCustomerDataOne} from 'api/customers'
// import PageHeader from 'components/common/menu/PageHeader'
import {Message} from "element-ui";



export default {
  name: 'CustomerForm',
  components: {
    // PageHeader
  },
  props: {
    customeruuid: {
      type: String, //指定传入的类型
      //type 也可以是一个自定义构造器函数，使用 instanceof 检测。
      default: '' //这样可以指定默认的值
    }
  },
  data() {
    return {
      labelPosition: 'left',
      ruleForm: {
        CustomerCode: '',
        CustomerName: '',
        Department1: '',
        Department2: '',
        Department3: '',
        Personnel: '',
        PostalNumber: '',
        Address1: '',
        Address2: '',
        Address3: '',
        Telephone: '',
        Fix: '',
        uuid: ''
      },
      rules: {
        CustomerName: [
          {required: true, message: '得意先名を入力してください', trigger: 'blur'}
        ],
        CustomerCode: [
          {required: true, message: '得意先CDを決めてください', trigger: 'blur'}
        ],
        Department1: [
          {required: true, message: '部署Iは必須項目です。', trigger: 'blur'}
        ],
        Personnel: [
          {required: true, message: '担当者は誰ですか？', trigger: 'blur'}
        ],
        Telephone: [
          {required: true, message: '電話番号を入力してください', trigger: 'blur'}
        ],
        PostalNumber: [
          {required: true, message: '郵便番号を入力してください', trigger: 'blur'},
          {min: 7, max: 8, message: '正しい郵便番号を入力してください', trigger: 'blur'}
        ],
        Address1: [
          {required: true, message: '住所を入力してください', trigger: 'blur'}
        ],
        Address2: [
          {required: true, message: '番地まで入力してください', trigger: 'blur'}
        ]
      }
    }
  },
  created: function () {
    if (this.customeruuid) {
      this.getCustomerDataOne()
    }
  },
  methods: {
    postCustomerData() {
      let values = {}
      values['CustomerCode'] = this.ruleForm.CustomerCode
      values['CustomerName'] = this.ruleForm.CustomerName
      values['Department1'] = this.ruleForm.Department1
      values['Department2'] = this.ruleForm.Department2
      values['Department3'] = this.ruleForm.Department3
      values['Personnel'] = this.ruleForm.Personnel
      values['PostalNumber'] = this.ruleForm.PostalNumber
      values['Address1'] = this.ruleForm.Address1
      values['Address2'] = this.ruleForm.Address2
      values['Address3'] = this.ruleForm.Address3
      values['Telephone'] = this.ruleForm.Telephone
      values['Fix'] = this.ruleForm.Fix
      values['uuid'] = this.customeruuid
      postCustomerData(values).then(res => {
        if (res.status == 200) {
          this.$emit('submit')
          Message({
            showClose: true,
            message: '顧客情報登録・更新成功しました。',
            type: 'success'
          });
        } else {
          this.$emit('cancel')
          Message({
            showClose: true,
            message: '顧客情報登録・更新失敗しました。',
            type: 'error'
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getCustomerDataOne() {
      let values = {}
      values['uuid'] = this.customeruuid
      getCustomerDataOne(values).then(res => {
        this.ruleForm.CustomerCode = res.data.data[0].CustomerCode
        this.ruleForm.CustomerName = res.data.data[0].CustomerName
        this.ruleForm.Department1 = res.data.data[0].Department1
        this.ruleForm.Department2 = res.data.data[0].Department2
        this.ruleForm.Department3 = res.data.data[0].Department3
        this.ruleForm.Personnel = res.data.data[0].Personnel
        this.ruleForm.PostalNumber = res.data.data[0].PostalNumber
        this.ruleForm.Address1 = res.data.data[0].Address1
        this.ruleForm.Address2 = res.data.data[0].Address2
        this.ruleForm.Address3 = res.data.data[0].Address3
        this.ruleForm.Telephone = res.data.data[0].Telephone
        this.ruleForm.Fix = res.data.data[0].Fix
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postCustomerData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 调用了别人的接口服务，不知道什么时候会停止
    getAddress() {
      getAddress({
        url: '/?zipcode=' + this.ruleForm.PostalNumber,
        method: 'get'
      }).then(res => {
        console.log(res.data)
        if (res.data.code === 200) {
          this.ruleForm.Address1 = res.data.data.pref
          this.ruleForm.Address2 = res.data.data.address
        } else {
          alert('住所を見つけませんでした!')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // resetForm(formName) {
    //   this.$refs[formName].resetFields()
    // },
    cancelForm() {
      this.$emit('cancel')
    },
    searchAddress() {
      this.getAddress()
    }
  }
}
</script>

